body {
    font-size: 16px;
    font-family: Roboto;
}
.container {
    width: 100%;
    height: 100vh;
    background-color: #13407a;
    background-image: url('./fondo3.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}
.flex {
    display: flex;
}
.column {
    flex-direction: column;
}
.center {
    justify-content: space-around;
    align-items: center;
}
.justify-center {
    justify-content: center;
}
.height-complete {
    height: calc(100% - 105px);
    overflow-y: auto;
}
.progress-text {
    text-align: center;
    color: #2196f3;
}
.login-wrap {
    background-color: white;
    display: flex;
    width: 30rem;
    height: 27rem;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    padding: 20px;
    border-radius: 6px;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
    -moz-box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
    box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
}

.form-group {
    padding: 8px;
}
.form-group input {
    width: 280px;
}
.buttons-login {
    margin-top: 10px;
}
button {
    margin: 5px !important;
}
.menu-izquierdo {
    background-color: white;
    align-self: space-between;
    justify-self: flex-start;
    height: 80vh;
    width: 30%;
    border-radius: 5px;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
    -moz-box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
    box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
}
.menu-desencriptado {
    padding: 20px;
    text-align: center;
}
.menu-derecho {
    margin-left: 80px;
    width: 50%;
    height: 80vh;
    background-color: white;
    align-self: space-between;
    justify-self: flex-start;
    border-radius: 5px;
    -webkit-box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
    -moz-box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
    box-shadow: 1px 1px 5px 0px rgba(32, 41, 51, 0.45);
}
.menu-derecho nav {
    max-height: 78vh;
    overflow-y: auto;
}
.menu-derecho h2 {
    text-align: center;
}
.error-login {
    width: 70%;
    padding: 15px;
    background-color: rgb(250, 215, 215);
    border-radius: 5px;
    border: 1px solid red;
    color: rgb(252, 55, 55);
    font-size: 0.8rem;
    margin: 10px;
}
.grow-1 {
    flex-grow: 1;
}
.cerrar-sesion {
    justify-self: flex-end;
}
.modal {
    width: 60%;
    margin: auto;
    max-height: 700px;
}
.bitacora {
    max-height: 700px;
    overflow-y: auto;
}
.MuiTableCell-stickyHeader {
    background-color: #ffa726 !important;
}

@keyframes new-item {
    0% {
        background-color: white;
    }
    25% {
        background-color: #d6ecff;
    }
    100% {
        background-color: white;
    }
}
.new-item-list {
    background: white;
    animation-name: new-item;
    animation-duration: 4s;
}
